






import { Vue, Component, Prop } from 'vue-property-decorator'

// require svg icons
const svgFiles = require.context('@/assets/icons', false, /\.svg$/)
svgFiles.keys().forEach(fileName => svgFiles(fileName))

@Component
export default class SvgIcon extends Vue {
  @Prop({
    type: String,
    required: true,
    default: ''
  })
  public name!: string
}
