







import { Vue, Component, Watch } from 'vue-property-decorator'
import Editor from '@/layout/Editor.vue'
import SwaggerImporter from '@/components/SwaggerImporter/index.vue'
import { debounce } from 'decko'

@Component({
  components: {
    SwaggerImporter,
    Editor,
    Preview: () => import('@/layout/Preview.vue')
  }
})
export default class App extends Vue {
  get isPreview() {
    return this.$store.state.isPreview
  }

  @Watch('$store.state.workspace', { deep: true })
  @Watch('$store.state.project', { deep: true })
  @debounce
  stateChanged() {
    this.$store.dispatch('saveProject')
  }

  created() {
    // 尝试加载浏览器缓存内的项目数据
    this.$store.dispatch('loadAppState')
  }
}
